<template>
    <div class="content-box">
        <div class="title-box">
            <p class="header-line">
                <svg t="1579405448699" class="icon m-r-10 msg_icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3895" width="22" height="22">
                <path d="M778.24 430.08H245.76c-11.264 0-20.48 9.216-20.48 20.48v307.2c0 33.792 27.648 61.44 61.44 61.44h450.56c33.792 0 61.44-27.648 61.44-61.44V450.56c0-11.264-9.216-20.48-20.48-20.48zM409.6 716.8c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m0-143.36c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m143.36 143.36c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m0-143.36c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m143.36 143.36c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m0-143.36c0 11.264-9.216 20.48-20.48 20.48h-40.96c-11.264 0-20.48-9.216-20.48-20.48v-40.96c0-11.264 9.216-20.48 20.48-20.48h40.96c11.264 0 20.48 9.216 20.48 20.48v40.96z m40.96-307.2h-51.2v-20.48c0-22.528-18.432-40.96-40.96-40.96s-40.96 18.432-40.96 40.96v20.48H419.84v-20.48c0-22.528-18.432-40.96-40.96-40.96s-40.96 18.432-40.96 40.96v20.48h-51.2c-33.792 0-61.44 27.648-61.44 61.44v20.48c0 11.264 9.216 20.48 20.48 20.48h532.48c11.264 0 20.48-9.216 20.48-20.48v-20.48c0-33.792-27.648-61.44-61.44-61.44z" p-id="3896" :fill="eventMsgIconColor"></path>
                </svg>
                {{$t("eventMsg.eventList")}}
            </p>
        </div>
        <div class="select-box">
            <label class="m-r-5">{{$t('label.selectGroup')}}</label>
            <el-select v-model="groupname" :placeholder="$t('eventMsg.selectGroupHolder')" @change="selectGroup()" size="small" style="width: 180px" filterable>
                <el-option value="All">
                    {{$t('global.all')}}
                </el-option>
                <el-option
                v-for="item in groupData"
                :key="item.name"
                :label="item.name"
                :value="item.name">
                    <span style="float: left">
                        <i class="fa fa-windows m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.windows"></i>
                        <i class="fa fa-android m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.android"></i>
                        <i class="fa fa-linux m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.linux"></i>
                        <i class="fa fa-object-group m-r-5" aria-hidden="true" v-if="item.type== groupTypeArray.mix"></i>
                        {{ item.name}}
                    </span>

                </el-option>
            </el-select>

            <label  class="m-r-5 m-l-10">{{$t('label.selectType')}}</label>
            <el-select v-model="pkgname" :placeholder="$t('eventMsg.selectTypeHolder')" @change="selectType()" size="small" style="width: 15%" filterable>
                <el-option value="All">
                    {{$t('global.all')}}
                </el-option>
                <el-option 
                v-for="item in pkgnameTypeList"
                :key="item.value"
                :label=$t(item.label)
                :value="item.value">
                </el-option>
            </el-select>

            <label  class="m-r-5 m-l-10">{{$t('label.timePeriod')}}</label>
            <el-select v-model="eventDate" :placeholder="$t('eventMsg.selectTimePeriodHolder')" @change="selectDate()" size="small" style="width: 180px" filterable>
                <el-option value="All">
                    {{$t('global.all')}}
                </el-option>
                <el-option 
                v-for="item in dateRange"
                :key="item.value"
                :label=$t(item.label)
                :value="item.value">
                </el-option>
            </el-select>
            <div class="fr m-r-10"> 
                <el-input size="small" class="max-w-300 m-l-10" v-model="keywords" :placeholder="$t('eventMsg.keywordHolder')" @keyup.enter.native="searchEventMsg(keywords)">
                    <el-button slot="append" icon="el-icon-search"  @click.native.prevent="searchEventMsg(keywords)" ></el-button>
                </el-input>
            </div>
        </div>
        <div class="table-box">
            <el-table
                id="eventTable"
                ref="eventTable"
                :data="eventMsgTableList"
                @selection-change="selectItem"
                tooltip-effect="dark"
                style="width: 100%"
                :default-sort = "{prop: 'registrationDate', order: 'descending'}"
                v-loading="listLoading" 
                @filter-change="filterEventMsg"
                >
                <el-table-column
                type="selection"
                min-width="50">
                </el-table-column>
                 <el-table-column
                    :label="$t('eventMsg.level')"
                    column-key="type"
                    prop="type"
                    :filters="[{ text: $t('global.info'), value: 0 }, {text: $t('global.error'), value: 1}, {text: $t('global.warn'), value: 2}]"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <i class="fa fa-exclamation-triangle c-success fa-2x" v-if="scope.row.type == 0"></i>
                        <i class="fa fa-exclamation-triangle c-danger fa-2x" v-if="scope.row.type == 1"></i>
                        <i class="fa fa-exclamation-triangle c-warn fa-2x" v-if="scope.row.type == 2"></i>
                    </template>
                </el-table-column>

                <el-table-column
                prop="devname"
                :label="$t('eventMsg.devname')"
                min-width="100">
                </el-table-column>

                <el-table-column
                prop="funcId"
                :label="$t('eventMsg.type')"
                min-width="100">
                </el-table-column>

                <el-table-column
                prop="ts"
                :label="$t('eventMsg.ts')"
                min-width="110">
                    <template slot-scope="scope">
                        {{scope.row.ts|time}}
                    </template>
                </el-table-column>
                
                <el-table-column
                prop="content"
                :label="$t('eventMsg.content')"
                min-width="100">
                </el-table-column>
            </el-table>
            <div class="m-t-10 cf fr">
                <el-pagination
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="limit"
                :current-page="currentPage"
                :total="dataCount"
                v-show="isshow"
                background
                >
                </el-pagination>
            </div> 
            <btn-group 
            :selectedData="multipleSelection" 
            :isDelete="true" 
            tid="eid" 
            :delApi="batchDeleteEventMsg" 
            @result="isSuccess" 
            v-show="isShowBtnGroup"
            class="fl m-t-10 cf">
            </btn-group>
            <el-button 
            size="small" 
            @click="clearAll()" 
            type="primary" 
            class="fl m-t-10 cf m-l-10"
                v-show="isShowBtnGroup">
            {{$t('global.clearAll')}}
            </el-button>
        </div>
    </div>
</template>
<script>
    import handleResponse from '../restfulapi/handleResponse'
    import swalFun from "../../assets/js/swal"
    import {
        getEventMsgByPageApi,
        getEventMsgByPageInGroupApi,
        getEventMsgByPageAndTypeInGroupApi,
        getEventMsgByPageAndTypeApi,
        deleteEventMsgApi,
        batchDeleteEventMsgStatusApi,
        deleteAllEventMsgApi,
        deleteAllEventMsgByGroupApi,
        getEventMsgByPageAndKeywordsApi,
        getEventMsgByPageAndKeywordsInGroupApi,
        getEventMsgByPageFromDateApi,
        getEventMsgByPageInGroupFromDateApi,
        getEventMsgByPageAndTypeInGroupFromDateApi,
        getEventMsgByPageAndTypeFromDateApi,
        deleteAllEventMsgAndTypeListApi,
        deleteAllEventMsgByGroupAndTypeListApi,
        getEventMsgByPageAndPkgnameApi,
        getEventMsgByPageAndPkgnameFromDateApi,
        getEventMsgByPageAndPkgnameInGroupApi,
        getEventMsgByPageAndPkgnameFromDateInGroupApi,

    } from "../restfulapi/eventMsgApi"

    import {getDeviceGroupApi} from "../restfulapi/deviceGroupApi"
    import btnGroup from "../../common/btn-group"
    import {typeArray, groupType} from "../../assets/js/constant"
    import {mapState} from 'vuex'

    export default {
        name: 'eventMsg',
        data() {
            let week = "eventMsg.week";
            let month = "eventMsg.month";
            let threeMonth = "eventMsg.threeMonth";
            //ms
            let weekSt = 7*24*60*60*1000;
            let monthSt = 30*24*60*60*1000;
            let threeMonthSt = 3*30*24*60*60*1000;
            return {
                eventMsgTableList: [],
                isshow: false,
                limit: 10,
                dataCount: 0,
                currentPage: 1,
                multipleSelection: [],
                isShowBtnGroup: false,
                clearLoading: false,
                batchDeleteEventMsg:  batchDeleteEventMsgStatusApi,
                typeArray: typeArray,
                groupData: [],
                groupname: "All",
                keywords: "",
                listLoading: false,
                eventMsgIconColor: "#303133",
                realKeywords: "",
                dateRange: [
                    
                    {label: week, value: weekSt},
                    {label: month, value: monthSt},
                    {label: threeMonth, value: threeMonthSt}
                ],
                pkgnameTypeList: [
                    {label: 'eventMsg.software', value: 'software'},
                    {label: 'eventMsg.hardware', value: 'hardware'},
                    {label: 'eventMsg.peripheral', value: 'peripheral'},
                    {label: 'eventMsg.connect', value: 'connect'}
                ],
                pkgname: "All",
                eventDate: "All",
                groupTypeArray: groupType
            }
        },
        
        components:{
            btnGroup,
        },

        methods: {

            searchEventMsg(keywords){
                let reg=new RegExp(/^[\u4E00-\u9FA5a-zA-Z0-9\.\-_:]{0,18}$/);
                if(!reg.test(keywords)){
                    this.$swal("", this.$t('eventMsg.deviceLikeName'), "info", {button: this.$t('global.confirm')})
                    return;
                }
               //work only by click search
                this.realKeywords = keywords
                
                this.eventDate = "All"
                this.pkgname = "All"
                this.groupname = "All"
                this.$refs.eventTable.clearFilter();
                this.typeArray = typeArray;

                this.currentPage = 1;
                this.getEventMsgLikeDevname(this.realKeywords);
            },

            getEventMsgLikeDevname(keywords){
                
                //clear filter
                this.listLoading = true;
                if(this.groupname && this.groupname != "All"){
                    getEventMsgByPageAndKeywordsInGroupApi(this.groupname, this.typeArray, keywords, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => { 
                            this.assignEventMsgData(res);
                        })
                    })
                }else{
                    getEventMsgByPageAndKeywordsApi(this.typeArray, keywords, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => { 
                            this.assignEventMsgData(res);
                        })
                    })
                }
            },

            selectGroup(){
                this.currentPage = 1;
                this.refreshEventMsg();
            },

            selectDate(){
                this.currentPage = 1;
                this.refreshEventMsg();
            },

            selectType(){
                this.currentPage = 1;
                this.refreshEventMsg();
            },

            refreshEventMsg(){
                this.initData();
                this.getEventMsg();
            },

            selectItem(val) {
                this.multipleSelection = val;
            },

            handleCurrentChange(currentPage){
                if(currentPage){
                    this.currentPage = currentPage;
                }else{
                    this.currentPage = 1
                }
                if(this.realKeywords){
                    this.getEventMsgLikeDevname(this.realKeywords);
                }else{
                    this.getEventMsg();
                }

            },

            getAllDeviceGroups(){
                getDeviceGroupApi().then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.groupData = res;
                        }
                    })
                })
            },

            getEventMsg(){
                this.listLoading = true;
                if(this.groupname === "All"){
                    if(this.pkgname =="All"){
                        if(this.eventDate == "All"){
                            this.getAllEventMsg();
                        }else{
                            this.getAllEventMsgFromDate();
                        }
                    }else{
                        if(this.eventDate == "All"){
                            this.getAllEventMsgByPkgname();
                        }else{
                            this.getAllEventMsgByPkgnameFromDate();
                        }
                    }  
                }else{
                    if(this.pkgname =="All"){
                        if(this.eventDate == "All"){
                            this.getEventMsgByGroup();
                        }else{
                            this.getEventMsgByGroupFromDate();
                        }
                    }else{
                        if(this.eventDate == "All"){
                            this.getAllEventMsgByGroupAndPkgname();
                        }else{
                            this.getAllEventMsgByGroupAndPkgnameFromDate();
                        }
                    }
                    
                }
            },

            getEventMsgByGroup(){
                if(!this.groupname){
                    console.error("[getAppStatusHistoryByGroupname]groupname is empty")
                }
                getEventMsgByPageAndTypeInGroupApi(this.groupname, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                

            },

            getEventMsgByGroupFromDate(){
                if(!this.groupname){
                    console.error("[getAppStatusHistoryByGroupname]groupname is empty")
                }
                getEventMsgByPageAndTypeInGroupFromDateApi(this.eventDate, this.groupname, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                

            },

            getAllEventMsg(){
                getEventMsgByPageAndTypeApi(this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },
            getAllEventMsgFromDate(){
                getEventMsgByPageAndTypeFromDateApi(this.eventDate, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },

            getAllEventMsgByPkgname(){
                getEventMsgByPageAndPkgnameApi(this.pkgname, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },

            getAllEventMsgByPkgnameFromDate(){
                getEventMsgByPageAndPkgnameFromDateApi(this.pkgname,this.eventDate, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },

            getAllEventMsgByGroupAndPkgname(){
                getEventMsgByPageAndPkgnameInGroupApi(this.groupname, this.pkgname, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },

            getAllEventMsgByGroupAndPkgnameFromDate(){
                getEventMsgByPageAndPkgnameFromDateInGroupApi(this.groupname, this.pkgname, this.eventDate, this.typeArray, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignEventMsgData(res);
                    })
                })
                
            },

            assignEventMsgData(res){
                if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(res.data) === '[object Array]'){
                    this.dataCount = res.count;
                    this.eventMsgTableList = res.data;
                    this.isshow = this.dataCount > this.limit;
                    this.isShowBtnGroup = this.dataCount > 0;
                    let isFinish = true;
                
                }
            },

            deleteEventMsg(eid){
                _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.listLoading = true;
                        deleteEventMsgApi(eid).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.refreshEventMsg();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                }) 
            },


            filterEventMsg(value){
                this.keywords = "";
                this.realKeywords = "";
                this.typeArray = value.type;
                this.currentPage = 1;
                if(_g.isArray(this.typeArray)){
                    if(this.typeArray.length == 0){
                        this.typeArray = typeArray;
                    }
                    this.getEventMsg();
                }
            },

            //delete appstatus $emit
            isSuccess(result){
                if(result){
                    this.refreshEventMsg();
                }
            },

            //clear All history
            clearAll(){
                _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.clearLoading = true;
                        if(this.groupname == "All"){
                            deleteAllEventMsgAndTypeListApi(this.typeArray).then((data) => {
                                this.clearLoading = false;
                                handleResponse((data), (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.refreshEventMsg();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }else{
                            deleteAllEventMsgByGroupAndTypeListApi(this.groupname, this.typeArray).then((data) => {
                                this.clearLoading = false;
                                handleResponse((data), (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.refreshEventMsg();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }  
                    }
                })

            },

            initData(){
                this.eventMsgTableList = [];
                this.isShowBtnGroup= false;
                this.isshow= false;
                this.$refs.eventTable.clearFilter();
                this.typeArray = typeArray;
                this.realKeywords = "";
                this.keywords = "";
            }
        },

        created(){
            this.getAllDeviceGroups();
            this.getEventMsg();
        },

         watch:{
            onlineDeviceCount(){
                this.getEventMsg();
            }
        },
        
        computed:{
            ...mapState({
                onlineDeviceCount: "onlineDeviceCount"
            }),   
        },

    }
</script>

<style lang="scss" scoped>
@import "../../assets/css/colors";
    .msg_icon{
        position: relative;
        top: 4px;
    }
    .error_icon{
        margin-right: 4px;

    }
    .icon{
        margin-right: 5px;
        position: relative;
        top: 4px;
    }


    
</style>



